window.dom = {

  // æŸ¥

  create(string) {
    // return document.createElement(tagName);
    const container = document.createElement("template");
    container.innerHTML = string.trim();
    return container.content.firstChild;
  },
  after(node, node2) {
    node.parentNode.insertBefore(node2, node.nextSibling);
  },
  before(node, node2) {
    node.parentNode.insertBefore(node2, node);
  },
  append(parent, node) {
    parent.appendChild(node);
  },
  wrap(node, parent) {
    dom.before(node, parent)
    dom.append(parent, node)
  },

  // åˆ 

  remove(node) {
    node.parentNode.removeChild(node) //è®©è¿™ä¸ªèŠ‚ç‚¹çš„çˆ¸çˆ¸,åˆ é™¤å®ƒçš„å„¿å­
    return node //è¿”å›ç§»é™¤çš„å¯¹è±¡
  },
  empty(node) { //å¹²æ‰è¿™ä¸ªèŠ‚ç‚¹çš„æ‰€æœ‰å„¿å­
    // node.innerHTML = '' å¯ä»¥ç”¨è¿™ä¸ª,ä½†æ˜¯ä¸èƒ½è·å–å¼•ç”¨èŠ‚ç‚¹
    // const childNodes = node.childNodes è¿™ä¸ªæ˜¯è¯­æ³•
    // const { childNodes } = node  ä¸Šé¢è¯­æ³•çš„ç®€å†™
    const array = []  //å¼•ç”¨,éœ€è¦åˆ›å»ºä¸€ä¸ªç©ºæ•°ç»„
    let x = node.firstChild //æ‰¾åˆ°ç¬¬ä¸€ä¸ªå„¿å­
    while (x) { //å½“xå­˜åœ¨æ—¶,æˆ‘å°±æŠŠä»–ç§»é™¤,ç„¶åæ”¾åˆ°æˆ‘çš„æ•°ç»„é‡Œé¢
      array.push(dom.remove(node.firstChild))
      x = node.firstChild
    }
    return array
  },


  //æ”¹ 

  attr(node, name, value) { //è¿™å«é‡è½½
    if (arguments.length === 3) {
      node.setAttribute(name, value)
    } else if (arguments.length === 2) {
      return node.getAttribute(name)
    }
  },
  test(node, string) { //é€‚é…
    if (arguments.length === 2) {
      if ('innerText' in node) {
        node.innerText = string //ie
      } else {
        node.textContent = string //firefox/Chrome
      }
    } else if (arguments.length === 1) {
      if ('innerText' in node) {
        return node.innerText
      } else {
        return node.textContent
      }
    }
  },
  html(node, string) {
    if (arguments.length === 2) {
      node.innerHTML = string
    } else if (arguments.length === 1) {
      return node.innerHTML
    }
  },
  style(node, name, value) {
    if (arguments.length === 3) {
      //dom.style(div,'color','red')
      node.style[name] = value
    } else if (arguments.length === 2) {
      if (typeof name === 'string') {
        //dom.style(div,'color')
        return node.style[name]
      } else if (name instanceof Object) {
        //dom.style(div,{color:'red'})
        const object = name
        for (let key in object) {
          node.style[key] = object[key]
        }
      }
    }
  },
  class: {
    add(node, className) {
      node.classList.add(className)
    },
    remove(node, className) {
      node.classList.remove(className)
    },
    has(node, className) {
      return node.classList.contains(className)
    }
  },
  on(node, eventName, fn) {
    node.addEventListener(eventName, fn)
  },
  off(node, eventName, fn) {
    node.addEventListener(eventName, fn)
  },
  find(selector, scope) {
    return (scope || document).querySelectorAll(selector)
    //æ„æ€:å¦‚æœæœ‰scope,é‚£æˆ‘å°±åœ¨scopeé‡Œé¢è°ƒç”¨querySelectorAll;å¦‚æœæ²¡æœ‰,é‚£å°±åœ¨documenté‡Œé¢è°ƒç”¨querySelectorAll
  },
  parent(node) {
    return node.parentNode
  },
  children(node) {
    return node.children
  },
  siblings(node) {
    return Array.from(node.parentNode.children)
      //ä¼ªæ•°ç»„å˜æˆæ•°ç»„
      .filter(n => n !== node)
    //ä¹‹åå¯¹å®ƒè¿›è¡Œè¿‡æ»¤,åªè¦è¿™ä¸ªæ•°ç»„ä¸ç­‰äºå½“å‰è¿™ä¸ªèŠ‚ç‚¹,å°±æŠŠå®ƒæ”¾åˆ°è¿™ä¸ªæ•°ç»„é‡Œé¢
  },
  next(node) {
    let x = node.nextSibling
    while (x && x.nodeType === 3) {
      x = x.nextSibling
    }
    return x
  },
  previous(node) {
    let x = node.previousSibling
    while (x && x.nodeType === 3) {
      //å¦‚æœxå­˜åœ¨ä¸”ä¸æ˜¯æ–‡æœ¬,é‚£å°±è¿è¡Œä¸‹ä¸€ä¸ª,å¦‚æœæ˜¯å°±è¿”å›
      x = x.previousSibling
    }
    return x
  },
  each(nodeList, fn) {
    for (let i = 0; i < nodeList.length; i++) {
      fn.call(null, nodeList[i])
    }
  },
  index(node) {
    const list = dom.children(node.parentNode)
    //è·å–å®ƒçˆ¸çˆ¸çš„å„¿å­,å¹¶æŠŠå„¿å­å«list
    let i
    for (i = 0; i < list.length; i++) {//éå†è¿™ä¸ªlist
      if (list[i] === node) {//ç„¶åæ¯ä¸ªå’Œå®ƒåšå¯¹æ¯”
        break  //å¦‚æœç­‰äºå°±åˆ«åŠ¨äº†
      }
    }
    return i
  }
};



// dom.create = function () { }; å¯ä»¥å†™å¤–é¢,ä¹Ÿå¯ä»¥å†™window.domé‡Œé¢;